
import axios from "axios";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { Form, Field } from "vee-validate";
import * as Yup from "yup";
import { ref } from "vue";

export default {
  name: "Signup",
  components: {
    Form,
    Field,
  },
  setup() {
    const schema = Yup.object().shape({
      position: Yup.string().required("  ."),
      name: Yup.string().required("    ."),
      birth: Yup.string()
        .required("   .")
        .matches(
          /^\d{4}-(0[1-9]|1[012])-(0[1-9]|[12][0-9]|3[01])$/,
          " YYYY-MM-DD   ."
        ),
      school: Yup.string().required("   ."),
      grade: Yup.string().required("   ."),
      class_number: Yup.string().required("   ."),
      student_number: Yup.string().required("   ."),
      user_id: Yup.string().required("   ."),
      email: Yup.string()
        .required("Email    .")
        .email("Email   ."),
      password: Yup.string()
        .min(6, "  6  .")
        .required("   ."),
      confirmPassword: Yup.string()
        .oneOf([Yup.ref("password"), null], "  .")
        .required("    ."),
    });

    const store = useStore();
    const router = useRouter();

    let bool = ref(false);
    const onSubmit = (values: any) => {

      if (bool.value) {
        alert(" .");
      } else {
        axios
          .post(process.env.VUE_APP_API_URL + "/users/register/", values)
          .then((res) => {

            // alert("SUCCESS!! :-)\n\n" + JSON.stringify(values, null, 4));
            const payload = {
              user_id: values.user_id,
              password: values.password,
            };
            store.dispatch("accountStore/getToken", payload);
          })
          // .catch((err) => {
          //   console.log(err);
          // });
      }
    };
    const memberCheck = () => {
      axios
        .get(process.env.VUE_APP_API_URL + "/users/IdCheck/" + idCk.value)
        .then((response) => {

          let cnt = response.data;
          if (cnt) bool.value = true;
          else bool.value = false;
        })
        // .catch((error) => {
        //   console.log(error);
        // });
    };

    const idCk = ref("");
    const position = ref("");
    // const grade = ref(0);
    // const class_number = ref(0);
    // const student_number = ref(0);

    return {
      position,
      // grade,
      // class_number,
      // student_number,
      schema,
      idCk,
      bool,
      onSubmit,
      memberCheck,
    };
  },
};
